@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

:root {
  /* Primary  */
  --Red: hsl(0, 100%, 74%);
  --Green: hsl(154, 59%, 51%);
  --border-color: hsl(0, 0%, 80%);
  --green-bg: hsl(154, 65%, 68%);
  --green-shadow: hsl(154, 34%, 50%);
  --form-shadow: hsl(359, 43%, 61%);
  /* Accent */
  --Blue: hsl(248, 32%, 49%);
  --blue-hover: hsl(247, 28%, 58%);
  /* Neutral */
  --Dark-Blue: hsl(249, 10%, 26%);
  --Grayish-Blue: hsl(246, 25%, 77%);
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  min-height: 100vh;
  /* Ensure body takes up full viewport height */
  background: url(./image/bg-intro-mobile.png);
  background-color: var(--Red);
  font-family: 'Poppins', sans-serif;
  font-size: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

section {
  width: 90%;
  margin: auto;
}

section:first-of-type {
  margin-top: 3rem;
  text-align: center;
  color: white;
  margin-bottom: 3rem;
  padding: 0 0.6rem;
}

.blue-background {
  color: white;
  background-color: var(--Blue);
  text-align: center;
  padding: 0.8rem 4rem;
  border-radius: 5px;
  box-shadow: 0px 5px 2px 0px var(--form-shadow);
  margin-bottom: 1rem;
  font-weight: 700;
  transition: transform 0.2s, background-color 0.3 ease-in-out;
}

.blue-background span {
  font-weight: 400;
  opacity: 0.9;
}

.blue-background:hover {
  transform: translateY(2px);
  background-color: var(--blue-hover);
}

section:first-of-type h1 {
  padding-bottom: 1rem;
  font-weight: 700;
}

form {
  display: flex;
  flex-direction: column;
  background-color: white;
  padding: 1.4rem;
  gap: 0.6rem;
  border-radius: 10px;
  box-shadow: 0px 5px 2px 0 var(--form-shadow);
}

input {
  border: 1px groove var(--border-color);
  box-sizing: border-box;
  border-radius: 4px;
  padding: 1rem;
  outline: none;
  color: var(--Dark-Blue);
  font-weight: 700;
}

input:focus {
  border: 1px solid var(--Dark-Blue);
}

input::placeholder {
  font-weight: 600;
}

.green-background {
  background-color: var(--Green);
  box-shadow: 0px 4px 2px 0 var(--green-shadow);
  color: white;
  text-transform: uppercase;
  font-weight: 700;
  cursor: pointer;
  transition: transform 0.1s, background-color 0.3s ease-in-out;
  outline: none;
  border: none;
}

.green-background:hover {
  transform: translateY(2px);
  background-color: var(--green-bg);
  border: transparent;
  outline: none;
}

.green-background:focus {
  border: none;
  outline: none;
}

.p-text {
  color: var(--Grayish-Blue);
  text-align: center;
  font-size: 0.75rem;
  padding: 0.6rem 0.8rem 0;
}

.p-text span {
  color: var(--Red);
  font-weight: 700;
}

.attribution {
  font-size: 0.9rem;
  text-align: center;
  padding: 1rem 2rem;
  color: white;
  opacity: 0.7;
}

.attribution a {
  text-decoration: none;
  color: var(--Dark-Blue)
}

.attribution a:hover {
  color: var(--Blue);
}

.input-error {
  background-image: url('./image/icon-error.svg');
  background-repeat: no-repeat;
  background-position: right 10px center;
  padding-right: 30px;
  border: 2px solid var(--Red);
}

.error-text {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-end;
  font-size: 0.8rem;
  color: var(--Red);
  font-weight: 500;
  font-style: italic;
}

input[name="Email"].input-error::placeholder {
  color: var(--Red);
}

/* WebKit browsers (e.g., Safari, Chrome) */
input[name="Email"].input-error::-webkit-input-placeholder {
  color: var(--Red);
}

/* Mozilla Firefox */
input[name="Email"].input-error::-moz-placeholder {
  color: var(--Red);
}

/* Microsoft Edge (pre-Chromium) */
input[name="Email"].input-error:-ms-input-placeholder {
  color: var(--Red);
}

/* Internet Explorer 10+ */
input[name="Email"].input-error:-ms-input-placeholder {
  color: var(--Red);
}

@media screen and (min-width: 768px) {
  body {
    background: url(./image/bg-intro-desktop.png);
    background-color: var(--Red);
    display: flex;
  }

  .App {
    align-items: center;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas:
      "header form"
      "footer footer";
  }


  section:first-of-type {
    max-width: 600px;
    text-align: left;
  }

  section:first-of-type h1 {
    font-size: 2.5rem;
  }

  section:nth-of-type(2) {
    max-width: 600px;
  }

  form {
    padding: 2rem;
  }

}